.Hangman {
    width: 400px;
    color: black;
    text-align: center;
    zoom: 1.3;
  }
  
  .Hangman img {
    border: 10px solid white;
    height: 200px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
      0 0 40px rgba(0, 0, 0, 0.06) inset;
  }
  
  .Hangman h2 {
    font-size: 1.50rem;
    font-weight: 400;
  }
  
  .Hangman-word {
    letter-spacing: 1em;
    margin: 0.4em -1em 0.2em 0;
    font-size: 2rem;
  }
  
  .Hangman-btns {
    text-align: center;
    display: inline-block;
    width: 250px;
    margin-top: 0;
  }
  .Hangman button {
    border: none;
    font-family: inherit;
    font-size: 1rem;
    cursor: pointer;
    width: 30px;
    margin: 5px 2px 2px 2px;
    letter-spacing: 1px;
    outline: none;
    background: #30070a;
    color: #fff;
    box-shadow: 0 6px rosybrown;
    padding-top: 4px;
  }
  
  .Hangman button:hover {
    background-color: #0097a7;
  }
  
  .Hangman button:disabled {
    background: #bdbdbd;
    color: #eeeeee;
    box-shadow: 0 6px #9e9e9e;
  }
  #reset {
    width: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
    background: #5a0606f6;
    box-shadow: 0 6px #0a010e;
  }
  #reset:hover {
    background: #380303;
  }